import { makeStyles, Typography } from '@material-ui/core';
import clsx from 'clsx';
import moment from 'moment';
import PropTypes from 'prop-types';
import React, { FC } from 'react';
import { Theme } from 'src/theme';
import { ProjectActivity } from 'src/types/project';
import Activity from './Activity';

interface ActivitiesProps {
  className?: string;
  activities: ProjectActivity[];
}

const useStyles = makeStyles((theme: Theme) => ({
  root: {},
  title: {
    marginBottom: theme.spacing(3)
  },
  group: {
    '& + &': {
      marginTop: theme.spacing(4)
    }
  },
  activity: {
    position: 'relative',
    '& + &': {
      marginTop: theme.spacing(3),
      '&:before': {
        position: 'absolute',
        content: '" "',
        height: 20,
        width: 1,
        top: -20,
        left: 20,
        backgroundColor: theme.palette.divider
      }
    }
  }
}));

const Activities: FC<ActivitiesProps> = ({
  activities,
  className,
  ...rest
}) => {
  const classes = useStyles();
  const todayItems = [];
  const lastWeekItems = [];

  // eslint-disable-next-line no-restricted-syntax
  for (const activity of activities) {
    if (moment(activity.createdAt).isSame(moment(), 'day')) {
      todayItems.push(activity);
    } else {
      lastWeekItems.push(activity);
    }
  }

  return (
    <div className={clsx(classes.root, className)} {...rest}>
      <Typography className={classes.title} variant="h3" color="textPrimary">
        Today
      </Typography>
      <div className={classes.group}>
        {todayItems.map(activity => (
          <Activity
            activity={activity}
            className={classes.activity}
            key={activity.id}
          />
        ))}
      </div>
      <div className={classes.group}>
        <Typography className={classes.title} variant="h3" color="textPrimary">
          Last week
        </Typography>
        {lastWeekItems.map(activity => (
          <Activity
            activity={activity}
            className={classes.activity}
            key={activity.id}
          />
        ))}
      </div>
    </div>
  );
};

Activities.propTypes = {
  activities: PropTypes.array.isRequired,
  className: PropTypes.string
};

export default Activities;
